import './piece.css'

import { Piece as PieceObject } from '../../../../../domain/piece'

export default function Piece(
  props: {
    piece: PieceObject
  }
) {

  const pieceColor = props.piece.color
  const pieceType = props.piece.toString().toLowerCase()
  const pieceSvg = piece_svgs[`${pieceColor}_${pieceType}`]

  return (
    <div className="piece">
      {pieceSvg}
    </div>
  );
}

